<template>
  <div >
      
      <van-search v-model="searchKeyword" placeholder="请输入主办方关键词" />
       <div class="gap"></div> 
      <div class="container">
        <div class="section" v-for="item in hostList" :key='item.id' @click="handleSelect(item.id,item.name)">
            <span class="char">{{item.name}}</span>
        </div>
      </div>  
      <div class="no-more">没有更多了~</div>

      <div class="bottom-gap"></div>  
      <div class="btn-bottom" @click="handleAdd">新建</div>  
  </div>
</template>

<script>
import bus from '@/utils/bus.js'

export default {
  components: {
  },
  data() {
    return {
      searchKeyword: "",
      // 分页参数
      page: {
        // 每页显示条数
        limit: 10,
        // 当前页码
        pageNo: 1,
      },
     hostList:[]
     
    };
  },
  created () {
    this.reqHostList()
  },
  
  methods: {
    handleSelect(id,name) {
      bus.$emit('hostObj', {id, name})
      
      // this.$route.params.hid = id;
      // this.$route.params.name = name;
      this.$router.back()
    },
     /**
     *   主办方列表
     */
    async reqHostList() {
      const res = await this.$request("host.hostList");
      console.log("主办方列表 :>> ", res);
      this.hostList = res
    },

    handleAdd() {
        this.$router.push({
            path:'/adminAddtUnit'
        })
    }
  },
};
</script>

